import { renderUI } from "./renderer"
import { Badge, Button, Checkbox, Column, Divider, Image, Input, Link, Row, Select, SelectOption, Text, Textarea } from "./ui"

const components = {
  Button,
  Text,
  Row,
  Column,
  Input,
  Textarea,
  Select,
  SelectOption,
  Checkbox,
  Image,
  Link,
  Divider,
  Badge,
}

export { components, renderUI }

export type AntispaceContext<ActionMap extends Record<string, any>> = {
  [K in keyof ActionMap]: {
    action: K
    values: ActionMap[K]
    meta: AntispaceMetadata
  }
}[keyof ActionMap]

export interface AntispaceAppManifest<T extends Record<string, AntispaceAppFunction> = Record<string, AntispaceAppFunction>> {
  name: string
  slug: string
  description?: string
  image?: string
  wantsPage: boolean
  wantsRefresh: boolean
  hotkey: string
  functions: T
}

export interface AntispaceAppFunction<Name extends string = string, Params = { [key: string]: any }> {
  type: "function"
  function: {
    name: Name
    description: string
    parameters: {
      type: "object"
      properties: {
        [key: string]: {
          type: "boolean" | "string" | "number" | "array" | "object"
          description: string
        }
      }
      required: string[]
    }
  }
}

export type AntispaceMetadata = {
  user: {
    id: string
    name: string
    theme: string
  }
}

export type AntispaceAIRequest<M extends AntispaceAppManifest> = {
  [K in keyof M["functions"]]: {
    name: M["functions"][K]["function"]["name"]
    parameters: M["functions"][K] extends AntispaceAppFunction<any, infer P> ? P : never
    meta: AntispaceMetadata
  }
}[keyof M["functions"]]
